.bebe{
    -webkit-animation: walk 7s linear infinite;
            animation: walk 7s linear infinite;
    position: absolute;
}

@-webkit-keyframes walk
{
    0%
    {
        margin: 30vh 0 0 199vh;
    }
    100%
    {
       margin: 30vh 0 0 -20vw;
    }
}

@keyframes walk
{
    0%
    {
        margin: 30vh 0 0 199vh;
    }
    100%
    {
       margin: 30vh 0 0 -20vw;
    }
}

.bebe2{
    -webkit-animation: walk2 8s linear infinite;
            animation: walk2 8s linear infinite;  
    position: absolute; 
}

@-webkit-keyframes walk2
{
    0%
    {
        margin: 40vh 0 0 199vh;
    }
    100%
    {
        margin: 40vh 0 0 -20vw;
    }
}

@keyframes walk2
{
    0%
    {
        margin: 40vh 0 0 199vh;
    }
    100%
    {
        margin: 40vh 0 0 -20vw;
    }
}

.bebe3{
    -webkit-animation: walk3 6s linear infinite;
            animation: walk3 6s linear infinite;   
    position: absolute; 
}

@-webkit-keyframes walk3
{
    0%
    {
        margin: 50vh 0 0 199vh;
    }
    100%
    {
       margin: 50vh 0 0 -20vw;
    }
}

@keyframes walk3
{
    0%
    {
        margin: 50vh 0 0 199vh;
    }
    100%
    {
       margin: 50vh 0 0 -20vw;
    }
}

.bebe4{
    -webkit-animation: walk4 10s ease infinite;
            animation: walk4 10s ease infinite;   
