.add1
{
    position: absolute;
    margin-top: -16vh;
    margin-left: 8vh;
    -webkit-transition: margin 0.1s linear;
    transition: margin 0.1s linear;
    opacity: 0;
    font-size: 4vh;
    font-weight: bold;
}

.bebe:active > .add1, .bebe2:active > .add1, .bebe3:active > .add1, .bebe4:active > .add1
{
    opacity:1;
    margin-top: -30vh;
}

